.menu {
  box-sizing: border-box;
  height: 100vh;

  color: $main-color;
  background-color: $menu-background-color;

  font-weight: 600;
  text-transform: uppercase;

  overflow: auto;
  padding-bottom: 64px;

  &__top {
    margin-bottom: 24px;

    @include on-tablet {
      margin-bottom: 8px;
    }

    position: sticky;
    top: 0;
    z-index: 1;

    background-color: $menu-background-color;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 27px;

    color: inherit;
    text-decoration: none;
  }

  &__book-a-ride {
    display: block;
    width: fit-content;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;

    padding-bottom: 7px;
    border-bottom: 1px solid $main-color;

    color: inherit;
    text-decoration: none;
  }
}
